@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap);
:root {
  --primary-cyan: hsl(171, 66%, 44%);
  --shadow-cyan: hsl(171, 46%, 44%);
  --primary-blue: hsl(233, 100%, 69%);
  --shadow-blue: hsl(233, 80%, 59%);
  --dark-gray: hsl(210, 10%, 33%);
  --light-gray: hsl(201, 11%, 66%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-size: cover;
  font-family: 'Bai Jamjuree',
    sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3 {
  color: hsl(210, 10%, 33%);
  color: var(--dark-gray);
  font-weight: 600;
  text-align: center;
}

h1 {
  font-size: 2.4rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 2.0rem;
}

p {
  color: hsl(201, 11%, 66%);
  color: var(--light-gray);
  font-weight: 400;
  font-size: 1.8rem;
  text-align: center;
}

@media only screen and (min-width: 375px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.8rem;
  }

  h3 {
    font-size: 2.4rem;
  }
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 3.6rem;
  }

  h2 {
    font-size: 3.2rem;
  }

  h3 {
    font-size: 2.8rem;
  }
}

.hero-section {
  width: 100vw;
  background-image: url(/clipboard/static/media/bg-header-mobile.611a5565.png);
  background-size: 100% auto;
  background-repeat: no-repeat;
  padding: 48px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-logo {
  position: relative;
  width: 100px;
  height: 100px;
  margin: 30px 0;
}

.hero-logo svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-content {
  max-width: 600px;
  margin-bottom: 30px;
}

.hero-title {
  margin: 0 auto;
  max-width: 260px;
  margin-bottom: 20px;
}

.hero-text {
  line-height: 1.4;
}

.hero-buttons {
  width: 100%;
  max-width: 600px;
  text-align: center;
}

.btn {
  width: 100%;
  padding: 10px 0;
  border-radius: 50px;
  color: white;
  letter-spacing: 1px;
  cursor: pointer;
}

.btn--green {
  margin-bottom: 16px;
  background-color: var(--primary-cyan);
  box-shadow: 0 2px 1px var(--shadow-cyan);
}

.btn--green:hover {
  background-color: var(--shadow-cyan);
}

.btn--blue {
  background-color: var(--primary-blue);
  box-shadow: 0 2px 1px var(--shadow-blue);
}

.btn--blue:hover {
  background-color: var(--shadow-blue);
}

@media only screen and (min-width: 375px) {
  .hero-title {
    max-width: 300px;
  }

  .btn {
    padding: 14px 0;
  }
}

@media only screen and (min-width: 768px) {
  .hero-section {
    background-image: url(/clipboard/static/media/bg-header-desktop.3d39ff8e.png);
    background-size: contain;
  }

  .hero-title {
    max-width: none;
  }

  .hero-buttons {
    display: flex;
    justify-content: center;
  }

  .btn {
    width: 40%;
  }

  .btn--green {
    margin-bottom: 0;
    margin-right: 20px;
  }
}
.snippets-section {
  width: 100vw;
  padding: 30px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.snippets-main {
  max-width: 600px;
}

.snippets-features {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.snippets-feature {
  max-width: 425px;
}

.snippets-title {
  margin: 40px 0 24px;
}

.snippets-subtitle {
  margin: 40px 0 10px;
}

.snippets-text {
  line-height: 1.5;
}

.snippets-img {
  width: 100%;
  margin: 60px 0 20px;
  max-width: 500px;
}

@media only screen and (min-width: 768px) {
  .snippets-content {
    display: flex;
    width: calc(100vw + 50px);
    transform: translateX(-50px)
  }

  .snippets-img {
    width: 60vw;
    max-width: 700px;
  }

  .snippets-features {
    /* width: 40vw; */
    padding: 50px;
    align-items: flex-start;
  }

  .snippets-subtitle {
    text-align: left;
    font-size: 0.9em;
  }

  .snippets-feature .snippets-text {
    text-align: left;
    font-size: 0.8em;
  }
}

@media only screen and (min-width: 768px) {
  .snippets-features {
    padding: 80px;
  }
}
.access-section {
  width: 100vw;
  padding: 30px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.access-title {
  margin: 40px 0 24px;
}

.access-text {
  line-height: 1.5;
  max-width: 600px;
}

.access-img {
  width: 100%;
  margin: 60px 0 20px;
  max-width: 500px;
}

@media only screen and (min-width: 768px) {
  .access-img {
    width: 70vw;
    max-width: 800px;
  }
}
.workflow-section {
  width: 100vw;
  padding: 30px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.workflow-main {
  margin-bottom: 80px;
}

.workflow-title {
  margin: 0 0 18px;
}

.workflow-tool {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.workflow-icon {
  position: relative;
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
}

.workflow-subtitle {
  margin: 20px 0;
}

.workflow-text {
  line-height: 1.5;
  max-width: 425px;
}

.workflow-logos {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.workflow-logo {
  margin-bottom: 60px;
}

@media only screen and (min-width: 768px) {
  .workflow-tools {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    max-width: 1000px;
    margin-bottom: 80px;
  }

  .workflow-tool {
    margin-bottom: 0;
  }

  .workflow-tool:nth-child(2) {
    margin: 0 50px;
  }

  .workflow-subtitle {
    font-size: 2rem;
  }

  .workflow-tool .workflow-text {
    font-size: 1.4rem;
  }

  .workflow-logos {
    flex-direction: row;
  }

  .workflow-logo {
    width: 20%;
    padding: 20px 30px;
    margin-bottom: 0;
  }
}

@media only screen and (min-width: 768px) {
  .download-buttons {
    display: flex;
    justify-content: center;
  }
}
.download-section {
  width: 100vw;
  padding: 30px 32px 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.download-title {
  margin: 40px 0 24px;
}

.download-text {
  line-height: 1.5;
  max-width: 600px;
  margin-bottom: 50px;
}

.download-buttons {
  width: 100%;
  max-width: 600px;
  text-align: center;
}
footer {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 50px;
  background-color: #f2f2f2;
  align-items: center;
}

.footer-logo {
  position: relative;
  width: 60px;
  height: 60px;
  margin-bottom: 50px;
}

.footer-logo svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

nav {
  margin-bottom: 20px;
}

nav ul {
  list-style: none;
  text-align: center;
}

nav ul li {
  color: var(--dark-gray);
  font-weight: 600;
  margin-bottom: 24px;
}

nav ul li a {
  cursor: pointer;
}

nav ul li a:hover {
  color: #222;
}

.footer-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
}

.footer-icon {
  fill: var(--dark-gray);
}

.footer-icon:hover {
  fill: #222;
}

@media only screen and (min-width: 768px) {
  footer {
    flex-direction: row;
    padding: 20px 50px;
  }

  .footer-logo {
    margin: 0 100px;
  }

  nav {
    margin-bottom: 0;
  }

  nav ul {
    display: grid;
    grid-template-columns: auto auto auto;
  }

  nav ul li {
    padding: 20px;
    margin-bottom: 0;
    text-align: left;
  }

  nav ul li:nth-child(2) {
    grid-area: 2/1/3/2;
  }

  nav ul li:last-child {
    grid-area: 1/3/2/4;
  }

  .footer-icons {
    position: absolute;
    top: 50%;
    right: 120px;
    transform: translateY(-50%);
  }
}
